@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@200&display=swap');

*{
  margin: 0;
  padding: 0;
  font-family: 'Mulish', sans-serif;
  box-sizing: border-box;
}
.navbar{
  background-color: rgb(255, 255, 255) !important;
}
/* .nav_bg{
  background-color: rgb(211, 12, 12) !important;
} */
.navbar a{
  font-size: 1.3rem !important;
  text-transform: capitalize;
  color: rgb(128, 0, 248)!important;
}
.navbar a:hover{
  color: rgb(4, 21, 175)!important;
}
.navbar-brand{
  color: rgb(128, 0, 248) !important;
  font-size: 1.9rem !important;
  font-weight: bold;
}
.menu_active{
font-weight: bold;
border-bottom: 1px solid brown !important;
}
#header{
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}
#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}
.brand-name{
  color: rgb(128, 0, 248);
}
#header h2{
  font-size: 48px;
  color: black;
}
/* btn-get-started{
  font-size: 16px;
  font-weight: 500;
  padding: 10px 30px;
} */
.btn-get-started{
  font-size: 16px;
  font-weight: 500;
  padding: 10px 30px;
  border: 2px solid rgb(128, 0, 248);
  border-radius: 50px;
  color: rgb(128, 0, 248);
  text-decoration: none;
  letter-spacing: 1px;
  transition: 1s;
  display: inline-block;
}
.btn-get-started:hover{
background-color: rgb(128, 0, 248);
color: #fff;
}
.header-img{
  text-align: right;
}
.animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
  width: 350px;
  height: 450px !important;
}
@-webkit-keyframes up-down{
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);

  }
}
@keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}
